:root {
  --primary-bg: #0e0d1a;
  --secondary-bg: #2c2f3b;
  --tertiary-bg: #353845;
  --primary-text: #ffffff;
  --secondary-text: #e0e0e0;
  --accent-color: #ffd100;
  --border-color: rgba(255, 255, 255, 0.2);
  --reagent-bg-even: #2c2f3b;
  --reagent-bg-odd: #353845;
}

body {
  background-color: var(--primary-bg);
  color: var(--primary-text);
  font-family: "Inter", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
  line-height: 1.6;
}

a {
  color: var(--accent-color);
  transition: color 0.3s ease;
}

a:hover {
  color: #ffe666;
  text-decoration: none;
}

.splash-container {
  max-width: 740px;
  margin: 0 auto;
  padding: 40px 20px;
  text-align: center;
}

.wow-logo {
  display: block;
  max-width: 200px;
  margin: 50px auto 20px;
}

h1,
h2,
h3 {
  font-weight: 700;
  letter-spacing: -0.5px;
}

h1 {
  color: var(--accent-color);
  font-size: 2.5rem;
  margin-bottom: 30px;
}

h2 {
  font-size: 2rem;
  margin-bottom: 30px;
}

h3 {
  color: var(--accent-color);
  font-size: 1.5rem;
  margin-bottom: 20px;
}

.search-container {
  position: relative;
  display: flex;
  justify-content: center;
  margin-bottom: 40px;
  width: 100%;
}

#recipe-search {
  width: 300px;
  padding: 10px 12px;
  font-size: 1rem;
  border: 1px solid var(--border-color);
  border-radius: 0px;
  background-color: var(--secondary-bg);
  color: var(--secondary-text);
  outline: none;
  text-align: center;
}

button {
  padding: 10px 12px;
  font-size: 1rem;
  font-weight: bold;
  background-color: var(--accent-color);
  border: none;
  border-radius: 3px;
  color: var(--primary-bg);
  cursor: pointer;
  transition: all 0.3s ease;
}

button:hover {
  background-color: #ffe666;
  transform: translateY(-1px);
}

.search-button {
  margin-left: 10px;
}

.results-container {
  position: relative;
  padding: 30px;
  background-color: var(--secondary-bg);
  border-radius: 6px;
}

.results-container h2 {
  margin-top: 0;
  color: var(--accent-color);
}

.results-container ul {
  list-style-type: none;
  padding: 0;
}

.results-container li {
  margin-bottom: 10px;
  color: var(--secondary-text);
}

.error {
  color: #ff6b6b;
  font-weight: bold;
}

.suggestions-list {
  list-style-type: none;
  padding: 0;
  margin-top: 10px;
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
  background-color: var(--tertiary-bg);
  border: 1px solid var(--border-color);
  width: 300px;
  z-index: 1000;
  overflow: hidden;
}

.suggestions-list li {
  padding: 6px 20px;
  cursor: pointer;
  text-align: left;
  color: var(--secondary-text);
  transition: background-color 0.3s ease;
  text-align: center;
}

.suggestions-list li:hover {
  background-color: rgba(255, 209, 0, 0.1);
}

.reagent-list {
  list-style-type: none;
  padding: 0;
  width: 100%;
  border-collapse: separate;
  border-spacing: 0;
  display: table;
}

.reagent-list li {
  display: table-row;
  transition: transform 0.3s ease;
}

.reagent-list li:nth-child(even) {
  background-color: var(--reagent-bg-even);
}

.reagent-list li:nth-child(odd) {
  background-color: var(--reagent-bg-odd);
}

.reagent-list li:hover {
  transform: translateY(-1px);
}

.reagent-image {
  width: 40px;
  height: 40px;
  display: table-cell;
  vertical-align: middle;
  padding: 14px;
}

.reagent-name {
  display: table-cell;
  padding: 10px;
  text-align: left;
  vertical-align: middle;
  color: var(--secondary-text);
  font-weight: 500;
}

.reagent-quantity {
  display: table-cell;
  padding: 10px;
  text-align: right;
  vertical-align: middle;
  font-weight: 700;
  color: var(--accent-color);
  min-width: 80px;
}

.crafted-item-image {
  width: 32px;
  height: 32px;
  margin-right: 10px;
  vertical-align: middle;
}

.craft-multiplier {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  justify-content: center;
}

.craft-multiplier label {
  margin-right: 10px;
  font-weight: 600;
  color: var(--accent-color);
}

#craftMultiplier {
  width: 40px;
  padding: 8px 12px;
  font-size: 1rem;
  border: 2px solid var(--border-color);
  border-radius: 6px;
  background-color: var(--secondary-bg);
  color: var(--secondary-text);
  text-align: center;
  transition: all 0.3s ease;
}

#craftMultiplier:focus {
  outline: none;
  border-color: var(--accent-color);
}

.pin-recipe-btn {
  padding: 8px 15px;
  margin-bottom: 30px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 20px;
  cursor: pointer;
  font-weight: 600;
  transition: all 0.3s ease;
}

.pin-recipe-btn:hover {
  background-color: #0061c9;
  transform: translateY(-1px);
}

.pinned-recipes-container {
  margin-top: 60px;
  padding: 30px;
  background-color: var(--secondary-bg);
  border-radius: 6px;
}

.pinned-recipes-container h2 {
  color: var(--accent-color);
  margin-top: -10px; /* Move the heading up */
  margin-bottom: 20px; /* Reduced from 30px */
}

.pinned-recipes-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 30px;
}

.pinned-recipe {
  position: relative;
  background-color: var(--tertiary-bg);
  padding: 20px;
  border-radius: 6px;
  transition: transform 0.3s ease;
}

.pinned-recipe:hover {
  transform: translateY(-1px);
}

.close-btn {
  position: absolute;
  top: 10px;
  right: 10px;
  background: none;
  border: none;
  color: var(--secondary-text);
  font-size: 1.2rem;
  cursor: pointer;
  transition: color 0.3s ease;
}

.close-btn:hover {
  color: var(--accent-color);
}

.pinned-recipe h3 {
  color: var(--accent-color);
  margin-top: 10px;
  margin-bottom: 20px;
  font-size: 1.2rem;
}

.pinned-recipe .reagent-list {
  font-size: 0.9rem;
}

.pinned-recipe .reagent-icon {
  width: 30px;
  height: 30px;
}

.pinned-recipe .craft-multiplier {
  margin-bottom: 15px;
}

.pinned-recipe .craft-multiplier label {
  font-size: 0.9rem;
  margin-right: 8px;
}

.pinned-recipe .craft-multiplier input {
  width: 60px;
  padding: 5px 8px;
  font-size: 0.9rem;
}

.footer {
  flex-shrink: 0;
  margin-top: auto;
  padding: 20px;
  text-align: center;
  color: var(--secondary-text);
  font-size: 0.9rem;
  background-color: var(--primary-bg);
  display: flex;
  flex-direction: column;
  align-items: center;
}

.footer-icon {
  width: 30px; /* Adjust this value to your preferred size */
  height: auto;
  margin-bottom: 10px; /* Space between the icon and the text */
}

.footer p {
  margin: 0;
}

.shopping-list-container {
  margin-top: 60px;
  padding: 30px;
  background-color: var(--secondary-bg);
  border-radius: 6px;
}

.shopping-list-container h2 {
  color: var(--accent-color);
  margin-top: -10px; /* Move the heading up */
  margin-bottom: 20px; /* Reduced from 30px */
}

@media (max-width: 768px) {
  .splash-container {
    padding: 20px;
  }

  #recipe-search {
    width: 100%;
  }

  .pinned-recipes-grid {
    grid-template-columns: 1fr;
  }
}

/* Add this at the end of your existing CSS */

.wowhead-link {
  display: inline-flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  text-decoration: none;
  gap: 5px;
}

.wowhead-link .iconsmall,
.wowhead-link .iconmedium,
.wowhead-link .iconlarge {
  display: block;
  margin-bottom: 5px;
}

.wowhead-link ins {
  text-decoration: none;
}

/* Ensure the text wraps properly if it's too long */
.wowhead-link span {
  max-width: 100%;
  word-wrap: break-word;
}

.logo-container {
  position: relative;
  display: inline-block;
}

.logo-tooltip {
  rotate: -2deg;
  visibility: hidden;
  width: 200px;
  background-color: #007bff;
  color: var(--primary-text);
  text-align: center;
  border-radius: 6px;
  padding: 5px 0;
  position: absolute;
  z-index: 1;
  bottom: 100px;
  left: 50%;
  margin-left: -100px;
  opacity: 0;
  transition: opacity 0.3s;
}

.logo-tooltip::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: #007bff transparent transparent transparent;
}

.logo-container:hover .logo-tooltip {
  visibility: visible;
  opacity: 1;
}
